export default {
	props: {
		tabBarIndex: {
			type: Number,
			default: 0
		},
		tabBars: {
			type: Array,
			default: () => {
				return [];
			}
		},
		tabbarWidth: {
			type: String,
			default: 'width:50%;'
		},
		isFlex: {
			type: Boolean,
			default: true
		}
	},
	data() {
		return {};
	},
	methods: {
		changeTabIndex(index) {
			this.$emit('changeTabbarIdx', index);
			// this.tabBarIndex = index;
		}
	}
};
